export default {
	data() {
		return {
			userinfo:{}
		}
	},
	onShow() {
		var me = this;
		var userinfo = me.getGlobalUser("userInfos");
		me.userinfo = userinfo;
	},
	methods: {
		wxlogin:function(e){
			var me = this;
			var userInfo = e.detail.userInfo;
			console.log("e.detail.userInfo:");
			console.log(userInfo);
			
			uni.login({
				provider:"weixin",
				success: (res) => {
					console.log("res:");
					console.log(res);
					var code = res.code;
					uni.request({
						url:this.mainUrl+"user/user/wxLogin?code="+code,
						method:"POST",
						success: (loginRes) => {
							console.log("loginRes:");
							console.log(loginRes);
							// userInfo.logined = true;
							if(loginRes.data.status == "success"){
								var openid = loginRes.data.data.openid;
								if(openid != null){
									userInfo.openid = openid;
									userInfo.logined = true;
									
									console.log("userInfo:");
									console.log(userInfo);
									
									uni.request({
										url:this.mainUrl+"user/user/saveUserInfo",
										header:{'content-type':"application/x-www-form-urlencoded"},
										data:userInfo
											// nickName:userInfo.nickName,
											// gender:userInfo.gender,
											// city:userInfo.city,
											// faceImg:userInfo.avatarUrl,
											// province:userInfo.province,
											// country:userInfo.country
										,
										method:"POST",
										success: (res) => {
											console.log(res);
											userInfo.userId = res.data.data;
											uni.setStorageSync("userInfos",userInfo);
											uni.navigateBack({
												delta:1
											});
										}
									});
								}else{
									var userid = loginRes.data.data;
									userInfo.userId = userid;
									userInfo.logined = true;
									console.log(userInfo);
									uni.setStorageSync("userInfos",userInfo);
									uni.navigateBack({
										delta:1
									});
								}
							}
							else if(loginRes.data.status=="fail"){
								console.log(res.errmsg);
							}
						}
					})
					
				}
			})
		},
